// Helper functions - ‡∏õ‡∏£‡∏±‡∏ö‡πÉ‡∏´‡πâ‡πÉ‡∏ä‡πâ local time
const getCurrentDatePath = () => {
  // ‡πÉ‡∏ä‡πâ local time ‡πÅ‡∏ó‡∏ô UTC
  const date = new Date();
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const day = date.getDate().toString().padStart(2, '0');
  return `${year}-${month}-${day}`;
};

const getCurrentTime = () => {
  // ‡πÉ‡∏ä‡πâ local time ‡πÅ‡∏ó‡∏ô UTC
  const date = new Date();
  const hours = date.getHours().toString().padStart(2, '0');
  const minutes = date.getMinutes().toString().padStart(2, '0');
  const seconds = date.getSeconds().toString().padStart(2, '0');
  return `${hours}:${minutes}:${seconds}`;
};

const formatDisplayDate = (date) => {
  // ‡∏£‡∏±‡∏ö Date object ‡∏´‡∏£‡∏∑‡∏≠‡∏™‡∏£‡πâ‡∏≤‡∏á‡πÉ‡∏´‡∏°‡πà‡∏ñ‡πâ‡∏≤‡πÑ‡∏°‡πà‡∏°‡∏µ
  const targetDate = date || new Date();
  const month = (targetDate.getMonth() + 1).toString().padStart(2, '0');
  const day = targetDate.getDate().toString().padStart(2, '0');
  const year = targetDate.getFullYear();
  return `${month}/${day}/${year}`;
};

// PM thresholds and color functions
export const getAirQualityColor = (status) => {
  const colors = {
    Excellent: '#1E88E5',
    Good: '#2DC653',
    Moderate: '#FECF3E',
    Unhealthy: '#FF9500',
    Hazardous: '#D02224'
  };
  return colors[status] || colors.Moderate;
};

export const formatPMValue = (value) => {
  const numericValue = parseFloat(value);
  if (isNaN(numericValue)) return value;
  return `${numericValue.toFixed(2)} Œºg/m¬≥`;
};

